.header {
  justify-content: space-between;
  width: auto;
  height: 48px;
  display: flex;
}

.header:has(.header__burger_menu:target) {
  overflow: hidden;
}

.header__close {
  width: 24px;
  height: 24px;
}

.header__burger_menu {
  opacity: 0;
  pointer-events: none;
  background-color: #253757;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.header__burger_menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.header__burger_menu__top_bar {
  justify-content: space-between;
  margin-bottom: 100px;
  display: flex;
}

.header__hire_us_button {
  letter-spacing: 3px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  border-bottom: 2px solid #2060f6;
  border-radius: 4px;
  height: 30px;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
  text-decoration: none;
}

.header__hire_us_button:hover {
  color: #2060f6;
  transform: scale(1.1);
}

.header__logo {
  width: 48px;
  height: 28px;
}

.header__menu {
  cursor: pointer;
  height: 48px;
}

@media (width >= 1024px) {
